<template>
  <v-flex md6 xs12>
    <v-card tile flat class="bordered" height="100%">
      <v-layout row align-center fill-height>
        <v-flex xs8 class="pa-1">
          <strong style="text-transform: capitalize;">{{ rule.label ? rule.label : rule.name }}</strong><br />
          {{ $t(`terminology.${rule.name}`) }}
        </v-flex>
        <v-divider vertical />
        <v-flex xs4 class="pa-1">
          <span v-if="!isBoolean(rule.value)">{{ isArray(rule.value, rule.name) }}</span>
          <v-icon v-else-if="isBoolean(rule.value) && rule.value" class="green--text">mdi-check</v-icon>
          <v-icon v-else class="red--text text--darken-1">mdi-close</v-icon>
        </v-flex>
      </v-layout>
    </v-card>
  </v-flex>
</template>

<script>
export default {
  name: 'RuleBlock',
  props: ['rule'],
  methods: {
    isBoolean(input) {
      return input === true || input === false;
    },
    isArray(input, category) {
      return Array.isArray(input) && input.length > 0 && input[0] !== ''
        ? this.$t(`terminology.${category}-${input[0]}`)
        : input;
    }
  }
};
</script>

<style scoped>
.bordered {
  border: 1px solid lightgray;
}
</style>
